class Engine {
  constructor() {
    this.datasets = []
    this.init = new Date()
  }

  list() {
    return this.datasets
  }

  exists(d) {
    return this.datasets.indexOf(d) !== -1 ? true : false
  }

  size(data) {
    return Buffer.byteLength(data) < 4 * 1024 * 1024
  }
}

function createEngine() {
  return new Engine()
}

export { createEngine }
